.ao-testimonials {
	font-family: "Inter", sans-serif;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem 0.5rem;
	gap: 2rem;
	background-color: #fff;
	color: #000;
	overflow: hidden;
	position: relative;
}
.ao-testimonials .ao-hero__reviews {
	justify-content: center;
}
@media (min-width: 1024px) {
	.ao-testimonials {
		padding: 4.875rem 1rem 8rem;
		gap: 4rem;
	}
}

.ao-testimonials__title {
	display: flex;
	flex-direction: column;
	gap: 0.625rem;
	text-align: center;
	align-items: center;
}

.ao-testimonials__title h2 {
	/* font-size: 1.75rem; */
	font-size: 20px;
	font-weight: 700;
	line-height: 1.2;
	margin: 0;
	margin-bottom: 0.8rem;
}
.ao-testimonials__title,
.ao-testimonials__testimonials{
	max-width: 315px;
	margin: 0 auto;
}
@media (min-width: 1024px) {
	.ao-testimonials__title h2 {
		/* font-size: 3.125rem; */
		font-size: 50px;
	}
	.ao-testimonials__title {
		margin: 0 auto;
		max-width: 1260px;
	}
}

.ao-testimonials__testimonials {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	overflow: hidden;
}

@media (min-width: 1024px) {
	.ao-testimonials__testimonials {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		margin: 0 auto;
		max-width: 1260px;
		overflow: hidden;
	}
}

.ao-testimonials__testimonial {
	display: flex;
	flex-direction: column;
	gap: 1.375rem;
	padding: 1.28rem;
	background-color: #eef7ff;
	border-radius: 10px;
	font-size: 0.875rem;
	color: black;
	max-width: 20.5rem;
	flex-shrink: 0;
}

@media (min-width: 1024px) {
	.ao-testimonials__testimonial {
		font-size: 1rem;
		align-self: start;
		flex-shrink: 1;
		max-width: 25.125rem;
		padding: 1.25rem;
	}
	.ao-testimonials--expanded .ao-testimonials__testimonial:nth-child(4),
	.ao-testimonials--expanded .ao-testimonials__testimonial:nth-child(5),
	.ao-testimonials--expanded .ao-testimonials__testimonial:nth-child(6) {
		/* transition: all 0.3s linear; */
		padding: 1.25rem;
		height: auto;
	}
	.ao-testimonials__testimonial:nth-child(4),
	.ao-testimonials__testimonial:nth-child(5),
	.ao-testimonials__testimonial:nth-child(6) {
		transition: all 0.3s linear;
		padding: 0;
		height: 0;
	}
	.ao-testimonials__testimonial--content,.ao-testimonials__person{
		overflow: hidden;
	}
}

.ao-testimonials__testimonial--content {
	display: flex;
	flex-direction: column;
	gap: 1rem;
}
.ao-testimonials__testimonial--content p{
	margin: 0;
}
.ao-testimonials__testimonial--content::after {
	content: "";
	width: 100%;
	height: 1px;
	background-color: #92c8bb66;
}

.ao-testimonials__person {
	display: flex;
	gap: 1rem;
}

.ao-testimonials cite .verified {
	display: flex;
	align-items: center;
	gap: 0.25rem;
}

.ao-testimonials__testimonee {
	max-width: 5rem;
	max-height: 5rem;
	border-radius: 50%;
}

.ao-testimonials__testimonial .ao-hero__star img {
	max-width: 1rem;
}

.ao-testimonials__testimonial blockquote {
	display: flex;
	flex-direction: column;
	/* font-size: 0.875rem; */
	font-size: 14px;
	gap: 1.125rem;
	margin: 0;
}

@media (min-width: 1024px) {
	.ao-testimonials__testimonial blockquote {
		font-size: 16px;
		/* font-size: 1rem; */
		margin: 0;
	}
}

.ao-testimonials__testimonial cite {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 0.625rem;
	font-size: 0.75rem;
	text-align: left;
}

@media (min-width: 1024px) {
	.ao-testimonials__testimonial cite {
		font-size: 0.875rem;
	}
}

.ao-testimonials__testimonial cite img {
	max-width: 1rem;
	height: auto;
}

.ao-testimonials__testimonial .verified {
	color: #00b43d;
	font-weight: 500;
	font-size: 0.625rem;
}

@media (min-width: 1024px) {
	.ao-testimonials .ao-hero__reviews-text p {
		font-size: 22px;
	}
}

.ao-testimonials__veil,
.ao-testimonials__load-more {
	display: none;
}

@media (min-width: 1024px) {
	.ao-testimonials__veil,
	.ao-testimonials__load-more {
		display: block;
	}

	.ao-testimonials__veil {
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 50%;
		background: linear-gradient(
			to bottom,
			rgba(255, 255, 255, 0) 0%,
			rgba(255, 255, 255, 1) 100%
		);
		z-index: 2;
		transition: all 0.6s ease-in-out;
	}

	.ao-testimonials__load-more {
		position: absolute;
		bottom: 1rem;
		left: 50%;
		transform: translateX(-50%);
		z-index: 3;
		background-color: white;
		color: #009f34;
		border: 2px solid #009f34;
		padding: 1.5rem 5rem;
		font-size: 1.25rem;
		font-weight: 700;
		cursor: pointer;
		transition: background-color 0.3s ease;
		line-height: 1;
		text-transform: uppercase;
		border-radius: 10px;
		margin-bottom: 2rem;
	}

	.ao-testimonials__load-more:hover {
		background-color: #004c8d;
		color: white;
		border: 2px solid #004c8d;
	}

	.ao-testimonials--expanded .ao-testimonials__veil {
		height: 0;
		visibility: hidden;
		opacity: 0;
	}

	.ao-testimonials--expanded .ao-testimonials__load-more {
		display: none;
	}
}
